        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-mapbox</span> <span class="hljs-attr">mapId</span>=<span class="hljs-string">"mapbox.streets"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-mapbox</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
