<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">appearance</span>: {},
        <span class="hljs-attr">geometryInstances</span>: {},
        <span class="hljs-attr">positions</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">100.1340164450331</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.05494287836128</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">108.08821010582645</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.05494287836128</span> }
        ]
      }
    },
    mounted() {
      <span class="hljs-keyword">this</span>.$refs.polylineGround.createPromise.then(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer, cesiumObject }</span>) =&gt;</span> {
